* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  border-radius: 10px;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  align-items: center;
  gap: 20px;
  display: flex;
}

.image {
  width: 500px;
  margin-top: 20px;
}

.title {
  font-weight: bold;
}

.loader {
  color: #212121;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: 400;
  display: block;
  position: relative;
}

.loader:after {
  content: "Loading";
  z-index: 5;
  position: relative;
}

.loader:before {
  content: "";
  height: 6px;
  width: 200px;
  background: linear-gradient(#ff3d00 100%, rgba(0, 0, 0, 0) 0) 0 0 / 0% no-repeat;
  border: 1px solid;
  border-radius: 10px;
  animation: lineGrow 10s linear infinite;
  position: absolute;
  bottom: 0;
}

@keyframes lineGrow {
  to {
    background-size: 100%;
  }
}

